function formValidator() {
  const form = document.querySelector('form');
  if (form == null) return;
  const inputElements = Array.from(form.querySelectorAll('input'));

  const validator = () => {
    const requiredFields = inputElements.filter(({required}: HTMLInputElement) => required);

    const valids = requiredFields.map((field: HTMLInputElement) => {
      let valid = true;
      if (field.pattern) {
        const regex = new RegExp(field.pattern);

        valid = regex.test(field.value);
      } else {
        valid = Boolean(field.value);
      }

      if (valid) {
        field.classList.remove('invalid');
      } else {
        field.classList.add('invalid');
      }
    });

    return false;
  }

  form.addEventListener("submit", (event: Event) => {
/*     event.preventDefault(); 
    fetch("http://127.0.0.1:8787/add", {
      method: 'POST',
      body: new FormData(form)
    })
    .then(response => {
      console.log(response.body);
    })
    .catch(error => {
      console.error(error);
    }) */
  });

  const domainPlaceholder = form.querySelector('.domain-placeholder');
  if (domainPlaceholder) domainPlaceholder.addEventListener('click', () => { 
    const input = form.querySelector('input[name=key]');
    if (input != null) (input as HTMLElement).focus();  
  })
}